header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.container{
  max-width: 500px;
  margin: auto;
  border: 2px solid gray;
  padding: 5px 20px;
}

.btn{
  display: inline-block;
  padding: 10px 30px;
  border-radius: 5px;
  font-size: .8rem;
  cursor: pointer;
  border: none;
  color: white;
}
input{
  margin: 10px;
}
.btn-block{
  background-color: black;
  color: white;
}

.add-form{
  display: flex;
  flex-direction: column;
  margin: 10px auto;
}
.form-control{
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
.form-control input{
  height: 20px;
}
.alert{
  background-color: red;
  padding: 5px;
  color: white;
  border: 2px solid black;
  margin-bottom: 5px;
  text-align: center;
}
.check-box{
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: none;
}
.check-box label{
  align-self: center;
}
.check-box input{
  margin-right: 200px;
  height: 30px;
  width: 20px;
}
.task{
  background-color: rgb(212, 212, 212);
  padding: 2px 10px;
  margin-bottom: 10px;
  font-size: 15px;
  border-radius: 5px;
}

.task.reminder{
  border-left: 5px solid green;
}



